import { useState } from "react";
import { useRouter } from "next/navigation";
import { Plus } from "lucide-react";
import { mockBuildings, calculateBuildingPaymentPercentage, Building } from "../data/mockData";
import { CircularProgress } from "./CircularProgress";

export function BuildingsDashboard() {
  const router = useRouter();
  const [buildings, setBuildings] = useState<Building[]>(mockBuildings);
  const [showAddDialog, setShowAddDialog] = useState(false);
  const [newBuildingName, setNewBuildingName] = useState("");

  const handleAddBuilding = (e: React.FormEvent) => {
    e.preventDefault();
    if (newBuildingName.trim()) {
      const newBuilding: Building = {
        id: Date.now().toString(),
        name: newBuildingName.trim(),
        floors: [],
      };
      mockBuildings.push(newBuilding);
      setBuildings([...mockBuildings]);
      setNewBuildingName("");
      setShowAddDialog(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="font-semibold text-gray-700">Buildings Dashboard</h1>
          <p className="text-gray-600">Monitor payment progress across all buildings</p>
        </div>

        {/* Buildings Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-6">
          {buildings.map((building) => {
            const paymentPercentage = calculateBuildingPaymentPercentage(building);
            
            return (
              <div
                key={building.id}
                onClick={() => router.push(`/building/${building.id}`)}
                className="bg-white rounded-lg shadow hover:shadow-lg transition-shadow cursor-pointer p-6"
              >
                <div className="flex items-start justify-between mb-4">
                  <div className="flex-1 mr-4">
                    <h3 className="font-semibold text-gray-700">{building.name}</h3>
                    <p className="text-sm text-gray-500">
                      {building.floors.length} {building.floors.length === 1 ? 'floor' : 'floors'}
                    </p>
                  </div>
                  <CircularProgress percentage={paymentPercentage} />
                </div>
                
                <div className="pt-4 border-t border-gray-100">
                  <div className="flex justify-between text-sm">
                    <span className="text-gray-600">Payment Status</span>
                    <span className="text-gray-600">
                      {paymentPercentage === 100 ? 'Complete' : 'In Progress'}
                    </span>
                  </div>
                </div>
              </div>
            );
          })}

          {/* Add Building Card */}
          <div
            onClick={() => setShowAddDialog(true)}
            className="bg-white rounded-lg shadow hover:shadow-lg transition-shadow cursor-pointer p-6 border-2 border-dashed border-gray-300 flex flex-col items-center justify-center min-h-[200px] hover:border-blue-400"
          >
            <div className="w-12 h-12 rounded-full bg-blue-50 flex items-center justify-center mb-3">
              <Plus className="w-6 h-6 text-blue-600" />
            </div>
            <h3 className="font-semibold text-gray-700">Add New Building</h3>
            <p className="text-sm text-gray-500 mt-1">Create a new building entry</p>
          </div>
        </div>
      </div>

      {/* Add Building Dialog */}
      {showAddDialog && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl p-6 w-full max-w-md">
            <h2 className="font-semibold text-gray-700">Add New Building</h2>
            <form onSubmit={handleAddBuilding}>
              <div className="mb-4">
                <label htmlFor="buildingName" className="block text-sm font-medium text-gray-700 mb-2">
                  Building Name
                </label>
                <input
                  type="text"
                  id="buildingName"
                  value={newBuildingName}
                  onChange={(e) => setNewBuildingName(e.target.value)}
                  placeholder="Enter building name"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  autoFocus
                />
              </div>
              <div className="flex gap-3 justify-end">
                <button
                  type="button"
                  onClick={() => {
                    setShowAddDialog(false);
                    setNewBuildingName("");
                  }}
                  className="px-4 py-2 text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 text-white bg-blue-600 rounded-md hover:bg-blue-700"
                >
                  Add Building
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
